.ca3-scroll-down-link {
  // height: 30px;
  // width: 25px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 80px;
  // text-align: center;
  // font-size: 70px;
  z-index: 1;
  // text-decoration: none;
  animation: ca3_fade_move_down 2s ease-in-out infinite;
}

/*animated scroll arrow animation*/
@-webkit-keyframes ca3_fade_move_down {
  0% {
    -webkit-transform: translateX(-50%) translate(0, -20px);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(-50%) translate(0, 20px);
    opacity: 0;
  }
}
@-moz-keyframes ca3_fade_move_down {
  0% {
    -moz-transform: translateX(-50%) translate(0, -20px);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -moz-transform: translateX(-50%) translate(0, 20px);
    opacity: 0;
  }
}
@keyframes ca3_fade_move_down {
  0% {
    transform: translateX(-50%) translate(0, -20px);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateX(-50%) translate(0, 20px);
    opacity: 0;
  }
}
